import React, { Component } from 'react';
import Paper from 'material-ui/Paper';
import './App.css';
import SvgCanvas from './SvgCanvas';
import SongDescription from './SongDescription';

export default class SongDetail extends Component {
  render() {
    return (
        <div id="container">
            <Paper id="songTitle">
                <h2>
                    ALMIFONIS/ROSA
                </h2>
            </Paper>
            <SvgCanvas />
            <SongDescription />
        </div>
    )
  }
}




