import React, { Component } from 'react';
import Paper from 'material-ui/Paper';
import './App.css';
import Buttons from './Buttons';

export default class SongDescription extends Component {
  render() {
        const descriptionStyle = {
            display: "grid",
            gridTemplateColumns: "2fr 1fr 1fr",
            gridTemplateRows: "250px 200px 50px",            
        }
        const titleStyle = {
            gridColumn: "1/2",
            gridRow: "1/2",
            padding: "10px",
            backgroundColor: "#D81159",
            color: "white",
        }
        const infoStyle = {
            gridColumn: "2/3",
            gridRow: "1/2",
            padding: "10px",
            backgroundColor: "#002349",
            color: "white",
        }
        const downloadStyle = {
            gridColumn: "3/4",
            gridRow: "1/2",
            padding: "10px",
            backgroundColor: "#FBB13C",
            color: "white",
        }
        const footerContainerStyle = {
            gridColumn: "1/4",
            gridRow: "2/3",
            backgroundColor: "#218380",
            color: "white",
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            padding: 10
        }
        const footerStyle = {
            fontWeight: "lighter",
            fontSize: 15,
        }
    return (
        <Paper id="songDescription" style={descriptionStyle}>
            <div style={titleStyle}>
                <h3>ALMIFONIS/ROSA</h3>
                <h4 style={titleStyle}>anonymous</h4>
            </div>
            <div style={infoStyle}>
                <div className="infoItems">
                    <h3>Info</h3>
                    <div><h5>Concordant Sources</h5></div>
                    <div>Cortona 1, Trem</div>
                    <div><h5>Previous Edition</h5></div>
                    <div>Harrison</div>
                </div>
            </div>
            <div style={downloadStyle}>
                <h3>Downloads</h3>
                <Buttons />
            </div>
            <div style={footerContainerStyle}>
                <p style={footerStyle}>Project Author: Karen Desmond</p>
                <p style={footerStyle}>Website Designer: Kevin Wang</p>
            </div>
        </Paper>
    )
  }
}




